* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

:root {
  --body-bg-color: #feffd2;
  --nav-bg-color: #ffeea9;
  --nav-color: #ffbf78;
  --hover-color: #ff7d29;
  --img-size: 200px;
}

body {
  background-color: var(--body-bg-color);
}

#no-overflow {
  overflow-y: hidden;
}

.none {
  display: none !important;
}

nav {
  background-color: var(--nav-bg-color);
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding: 20px;
  display: flex;
}

#search {
  background-color: var(--nav-color);
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 8px 15px;
}

#search:focus {
  border: none;
  outline: none;
}

.button {
  background-color: var(--nav-color);
  cursor: pointer;
  color: #000;
  border-radius: 10px;
  padding: 8px 10px;
  text-decoration: none;
}

.button:hover, #ingredients.selected {
  background-color: var(--hover-color);
}

#ingredients-finding {
  flex-flow: row;
  order: -2;
  justify-content: space-evenly;
  width: 100%;
  display: flex;
}

#search-ingredients {
  background-color: var(--nav-color);
  border: none;
  border-radius: 10px;
  outline: none;
  width: 70%;
  padding: 0 15px;
}

#search-ingredients:focus {
  border: none;
  outline: none;
}

#apply {
  cursor: pointer;
  background-color: var(--nav-color);
  border-radius: 10px;
  padding: 10px;
  display: block;
}

#apply:hover {
  background-color: var(--hover-color);
}

#ingredients-div {
  border: 1px solid var(--nav-color);
  z-index: 1;
  background-color: #fff;
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 50%;
  max-height: 300px;
  padding: 10px;
  display: flex;
  position: absolute;
  top: 4rem;
  overflow-y: scroll;
  transform: translateX(-50%);
  background-color: var(--nav-bg-color) !important;
}

.ingredient {
  text-align: center;
  cursor: pointer;
  background-color: var(--nav-color);
  text-transform: capitalize;
  border-radius: 10px;
  flex-grow: 1;
  padding: 10px;
  display: inline;
}

.ingredient.selected {
  background-color: var(--hover-color);
  order: -1;
}

#loading {
  background-color: var(--body-bg-color);
  z-index: 5;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

#loading-circle {
  border: 16px solid var(--nav-bg-color);
  border-top: 16px solid var(--nav-color);
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: 2s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

#data {
  flex-flow: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;
  width: 80%;
  margin: 5rem auto;
  display: flex;
}

#data img {
  width: var(--img-size);
  height: var(--img-size);
  aspect-ratio: 16 / 9;
  object-fit: contain;
  margin-bottom: 10px;
}

#data h1 {
  width: var(--img-size);
  text-align: center;
  overflow-wrap: word-wrap;
  font-size: 1.7rem;
}

#data p {
  text-align: center;
  font-size: .8rem;
}

.coctail {
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  display: inline-flex;
}

#data div:hover > img {
  opacity: .7;
}

#arrow-up {
  background-color: var(--nav-color);
  color: #000;
  border-radius: 50%;
  padding: 1rem;
  position: fixed;
  bottom: 2rem;
  left: 2rem;
}

#arrow-up:hover {
  background-color: var(--hover-color);
}

@media only screen and (width <= 600px) {
  #ingredients-finding {
    flex-flow: column wrap;
    justify-content: center;
    align-items: stretch;
    gap: 10px;
  }

  #search-ingredients {
    width: 100%;
    padding: 8px 15px;
  }

  #data img {
    width: 100px;
    height: 100px;
  }

  #data h1 {
    width: 100px;
    font-size: 1rem;
  }

  #arrow-up {
    padding: .2rem;
    left: 1rem;
  }
}
